
.infobox {
    width: 100%;
}
.info {
    width: 1280px;
    margin: 0 auto;
}
.info_list {
    width: 1280px;
    overflow: hidden;
    padding: 66px 0;
}
.info_list li {
    width: 280px;
    height: 54px;
    float: left;
    text-align: center;
    padding: 134px 0;
    margin: 0 20px;
    background: url('../../../static/images/indexinfo.png') no-repeat;
}
.info_list li p.title {
    height: 20px;
    line-height: 20px;
    font-size: 18px;
    color: #4e4e4e;
    font-weight: bold;
}
.info_list li p {
    height: 16px;
    line-height: 16px;
    font-size: 16px;
    color: #999;
    padding: 8px 0;
}
.info_list li p.title em {
    color: #ff930c;
}



