
.service {
    width: 100%;
    min-width: 1280px;
    background: #f5f6f8;
    text-align: center;
    border-top: 1px solid #dbdbdb;
    border-bottom: 1px solid #dbdbdb;
    padding: 67px 0;
}
.service .layout {
    width: 1280px;
    margin: 0 auto;
}
.service .layout p.title {
    font-size: 20px;
    color: #333;
    line-height: 20px;
}
.service .layout p {
    color: #4c4c4c;
    line-height: 14px;
    font-size: 14px;
    padding: 10px 0;
}
.service .layout p em{
	color: #48a2e0;
}



