
.serveinfo {
    width: 100%;
    background: #f5f6f8;
    border-bottom: 1px solid #dbdbdb;
}
.serveinfobox {
    width: 1280px;
    overflow: hidden;
    margin: 0 auto;
    padding: 20px 0 50px;
}
.serveinfo li {
    float: left;
    width: 500px;
    padding: 20px 70px;
}
.serveinfo li img {
    width: 500px;
    height: 240px;
    vertical-align: top;
}
.serveinfo li p.title {
    height: 25px;
    line-height: 25px;
    font-size: 27px;
    color: #4c4c4c;
    padding: 20px 10px;
}
.serveinfo li p {
    height: 60px;
    line-height: 20px;
    overflow: hidden;
    font-size: 16px;
    color: #4e4e4e;
    padding: 0 10px;
}



