html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 12px;
	font: inherit;
	vertical-align: baseline;
	font-family: "Microsoft YaHei", arial, Tahoma, "å®‹ä½“"
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
	display: block
}

body {
	line-height: 1;
	position: relative;
	min-width: 1280px;
}

ol,
ul {
	list-style: none
}

blockquote,
q {
	quotes: none
}

blockquote:before,
blockquote:after,
q:before,
q:after {
	content: '';
	content: none
}

table {
	border-collapse: collapse;
	border-spacing: 0
}

input,
textarea,
select {
	padding: 0;
	margin: 0
}

a {
	color: #666;
	text-decoration: none;
	font-size: 12px;
	cursor: pointer;
}

body {
	font: normal 12px arial, "Microsoft YaHei", "\5B8B\4F53";
	background: #FFF;
	color: #333
}

input,
select {
	vertical-align: middle;
	font: normal 12px arial, "Microsoft YaHei", "\5B8B\4F53"
}

textarea {
	font: normal 12px arial, "Microsoft YaHei", "\5B8B\4F53"
}

b,
i {
	font-style: normal
}

input,
textarea {
	outline: 0;
	resize: none
}
a{
	text-decoration: none;
}
li{
	list-style: none;
}

.clearfix{
	content: '';
	display: block;
	clear: both;
}
.fl{
	float: left;
}
.fr{
	float: right
}.wrap {
    width: 100%;
    animation: fadeIn 1.5s ease;
}
.center {
    text-align: center;
}


