
.footer{
	width: 100%;
	background: #5a5a5a;
}
.footer_box{
	width: 1280px;
	margin: 0 auto;
}
.footer_t{
	padding-top: 30px;
	width: 1280px;
	overflow: hidden;
}
.footer_t dl{
	display: inline-block;
	padding-right: 82px;
}
.footer_t dl dt {
    width: 100%;
    height: 20px;
    line-height: 20px;
    font-size: 16px;
    color: #fff;
    border-bottom: 1px solid #666;
    padding-bottom: 20px;
}
.footer_t dl dd{
	padding: 20px 0;
}
.corpinfo{
	width: 440px;
}
.corpinfo p{
	line-height: 18px;
	font-size: 14px;
	margin-bottom: 18px;
	color: #fff;
	text-indent:30px;
}
.corpinfo p:nth-child(1){
	background: url('../../static/images/ico2.png') no-repeat left;
}
.corpinfo p:nth-child(2){
	background: url('../../static/images/ico3.png') no-repeat left;
}
.corpinfo p:nth-child(3){
	background: url('../../static/images/ico4.png') no-repeat left;
}
.corpinfo p:nth-child(4){
	background: url('../../static/images/ico4.png') no-repeat left;
}
.qrcode{
	width: 116px;
}
.webmap {
    width: 180px;
    overflow: hidden;
    margin-left: -30px;
}
.webmap a {
    float: left;
    width: 60px;
    color: #fff;
    font-size: 14px;
    height: 14px;
    line-height: 14px;
    margin-bottom: 20px;
    margin-left: 30px;
}
.footer_t .last{
    width: 300px;
    float: right;
    padding: 0 10px;
}
.last .webmap{
	width: 240px;
}
.last .webmap a{
	width: 90px;
	float: left
}
.copyright {
    width: 1280px;
    height: 35px;
    line-height: 35px;
    color: #fff;
    font-size: 13px;
}



