
.switchoverbox {
    width: 100%;
}
.switchover {
    width: 1280px;
    margin: 0 auto;
}
.switchover_tag {
    width: 1280px;
    overflow: hidden;
    padding: 43px 0 41px;
    border-bottom: 1px solid #dbdbdb;
}
.switchover_tag a {
    float: left;
    width: 166px;
    height: 54px;
    line-height: 54px;
    color: #686868;
    background: #e5e5e5;
    font-size: 18px;
    margin: 0 77px;
    border-radius: 5px;
    text-align: center;
}
.switchover_tag a:hover, .switchover_tag a.hover {
    color: #fff;
    background: #4aa2e1;
}
.switchover_cntbox {
    width: 1280px;
    overflow: hidden;
    padding-bottom: 40px;
}
.switchover_cnt {
    width: 1280px;
    overflow: hidden;
}
.switchover_cnt .article1 {
    width: 880px;
    float: left;
    padding: 25px 40px 0;
}
.switchover_cnt_r {
    float: right;
    width: 320px;
}
.articlecss_row {
    font-size: 12px;
    line-height: 20px;
    text-indent: 2em;
}
.articlecss_tit {
    font-size: 14px;
    font-weight: bold;
}
.blue {
    color: #337FE5;
}
.articlecss p {
    font-size: 12px;
    line-height: 20px;
}
.switchover_cnt_r img {
    width: 320px;
    padding-top: 40px;
}
/* 旗下产品 */
.productbox {
    width: 1280px;
}
.product_tit {
    display: block;
    width: 1280px;
    height: 30px;
    text-align: center;
    padding: 50px 0;
}
.product_tit div {
    display: inline-block;
    width: 600px;
    height: 14px;
    border-bottom: 1px solid #4d4b4c;
    position: relative;
}
.product_tit div span {
    display: inline-block;
    width: 120px;
    height: 30px;
    line-height: 30px;
    padding: 0 10px;
    background: #fff;
    font-size: 20px;
    color: #4e4e4e;
    position: absolute;
    left: 230px;
    top: 0;
}
.product_list1, .product_list2 {
    width: 1280px;
    overflow: hidden;
}
.product_list1 ul, .product_list2 ul {
    width: 1310px;
    margin-left: -30px;
}
.product_list1 li {
    width: 250px;
    padding: 20px;
    text-align: center;
    margin-left: 30px;
    float: left;
}
.product_list1 li a {
    width: 250px;
    height: 158px;
    display: block;
}
.product_list1 li p.title {
    padding: 13px 0 5px;
}
.product_list1 li p.title a {
    height: 25px;
    line-height: 25px;
    color: #4d4d4d;
    font-size: 15px;
}
.product_list1 li a {
    width: 250px;
    height: 158px;
    display: block;
}
.product_list1 li p.desc {
    height: 75px;
    line-height: 25px;
    overflow: hidden;
    font-size: 15px;
    color: #a5a5a5;
    text-align: left;
}



