
.servesupport {
    width: 100%;
}
.servesupportbox {
    width: 1280px;
    margin: 0 auto;
}
.servesupport_desc {
    width: 1280px;
    padding: 50px 0;
    text-align: center;
    border-bottom: 1px solid #dbdbdb;
}
.servesupport_desc p.title {
    height: 26px;
    line-height: 26px;
    font-size: 26px;
    color: #343434;
    padding-bottom: 22px;
}
.servesupport_desc p {
    height: 14px;
    line-height: 14px;
    color: #9a9a9a;
    font-size: 14px;
}
.servesupport_list {
    width: 1200px;
    overflow: hidden;
    padding: 45px 40px;
}
.servesupport_list li {
    float: left;
    width: 320px;
    padding: 30px 40px;
}
.servesupport_list li img {
    display: block;
    width: 120px;
    height: 120px;
    vertical-align: top;
    margin: 0 auto;
}
.servesupport_list li p.title {
    font-size: 22px;
    height: 16px;
    line-height: 16px;
    text-align: center;
    color: #4d4d4d;
    padding: 30px 0 20px;
}
.servesupport_list li p {
    height: 100px;
    line-height: 25px;
    overflow: hidden;
    color: #7f7f7f;
    font-size: 17px;
}



