
.joinbox {
    width: 100%;
}
.join {
    width: 1280px;
    margin: 0 auto;
    overflow: hidden;
    padding: 30px 0;
}
.join_l {
    width: 1000px;
    float: left;
}
.join_r {
    width: 260px;
    float: right;
}
.join_tag {
    width: 1000px;
    overflow: hidden;
    border-bottom: 1px solid #dbdbdb;
    padding-bottom: 30px;
}
.join_tag span:hover, .join_tag span.hover {
    color: #fff;
    background: #4aa2e1;
}
.join_tag span {
    display: inline-block;
    width: 160px;
    height: 54px;
    line-height: 54px;
    font-size: 16px;
    background: #e5e5e5;
    color: #686868;
    margin: 0 45px;
    text-align: center;
    border-radius: 5px;
    float: left;
    cursor: pointer;
}
.join_list {
    width: 980px;
    padding: 0 10px;
    overflow: hidden;
}
.join_list .join_listcnt {
    width: 960px;
    float: left;
    padding: 10px 0;
}
.join_listcnt_tit {
    line-height: 25px;
    font-weight: bold;
    color: #464545;
}
.join_listcnt p {
    line-height: 25px;
    color: #464545;
}
.join_listcnt_tit em {
    padding-right: 20px;
    color: #337fe5;
}
.join_contact {
    width: 260px;
}
.join_contact_tit {
    width: 260px;
    height: 16px;
    line-height: 16px;
    font-size: 16px;
    color: #333;
    padding-bottom: 15px;
}
.join_contact p {
    line-height: 14px;
    font-size: 14px;
    color: #333;
    padding: 5px 0;
}



