
.plan {
    width: 100%;
    padding-top: 50px;
}
.planbox {
    width: 1280px;
    margin: 0 auto;
    padding-bottom: 50px;
}
.planbox_tit {
    width: 100%;
    height: 24px;
    line-height: 24px;
    font-size: 24px;
    padding-bottom: 40px;
    color: #4d4d4d;
}
.planbox dl {
    width: 1180px;
    overflow: hidden;
    margin: 0 50px;
    background: #eeeff1;
    padding: 30px 0;
    border-radius: 8px;
}
.planbox dl dt {
    width: 1120px;
    height: ;
    float: left;
    font-size: 25px;
    padding: 0 30px 10px 30px;
    color: #4e4e4e;
}
.planbox dl dd {
    width: 590px;
    height: 132px;
    float: left;
    padding: 50px 0;
}
.planbox dl dd img {
    width: 128px;
    height: 128px;
    padding: 0 35px;
    vertical-align: middle;
}
.planbox dl dd div {
    width: 380px;
    display: inline-block;
    vertical-align: top;
}
.planbox dl dd div p.title {
    height: 20px;
    line-height: 20px;
    font-size: 20px;
    color: #49a2dc;
    padding: 0 0 8px;
}
.planbox dl dd div p {
    width: 200px;
    height: 14px;
    line-height: 20px;
    font-size: 14px;
    color: #4d4d4f;
    padding-top: 10px;
}



